      -ms-user-select: none;
          user-select: none; }

.app {
  position: relative; }

.hidden {
  display: none; }

.page {
  position: relative;
  background: #7140eb; }

.mockup {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  visibility: hidden; }

.world {
  width: 100%;
  height: 100%; }
  .world > canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    cursor: pointer; }

.world__info {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background: var(--black-30);
  color: var(--foreground-negative);
  padding: 13px 30px;
  line-height: 1.1;
  border-radius: 30px; }

.aspect {
  position: relative;
  width: 100%;
  padding-top: 51.25%;
  overflow: hidden; }
  .aspect > img,
  .aspect > svg,
  .aspect > video,
  .aspect > canvas,
  .aspect > .video,
  .aspect > iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .aspect .world {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.world__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .world__background:before {
    content: '';
    background: #020024;
    background: radial-gradient(circle, rgba(2, 0, 36, 0.797879) 0%, rgba(2, 0, 36, 0.5) 35%, rgba(2, 0, 36, 0) 100%);
    position: absolute;
    opacity: 0.9;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    pointer-events: none; }
  .world__background:after {
    content: '';
    background: url("https://s.cdpn.io/1715/noise-1.png");
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    mix-blend-mode: multiply;
    opacity: 1;
    pointer-events: none; }
